@mixin t {
  @media only screen and (max-width: "699px") {
    @content; } }
@mixin m {
  @media only screen and (max-width: "599px") {
    @content; } }
@mixin sm {
 @media only screen and (max-width: "499px") {
   @content; } }
@mixin s {
	@media only screen and(max-width: "449px") {
		@content; } }
$white: #fff;
$black: #000;
$gray: #808080;
$gray-light: #f3f3f3;
$red: #a6334e;
$red-light: #900022;
* {
	box-sizing: border-box; }
a {
	text-decoration: none;
	color: $black; }
html, body {
	font-size: 18px;
	font-family: $PR;
	line-height: 1.35;
	color: $black; }

.out {}

.layout {
	max-width: 768px;
	margin-left: auto;
	margin-right: auto;
	padding:  0 15px;
	position: relative; }
.info-block {
	padding-bottom: 15px;
	@include clr;
	&__date, &__stat {
		font-size: 13px;
		line-height: 1.2;
		color: #888; }
	&__date {
		float: left;
		padding-top: 8px; }
	&__stat {
		text-transform: uppercase;
		float: right;
		span {
			@include s {
				display: none; } } }
	&__stat-link {
		display: inline-block;
		background: #808080;
		padding: 0 13px;
		height:  32px;
		line-height: 30px;
		color: $white;
		position: relative;
		&:before {
			content: "";
			position: absolute;
			bottom: -9px;
			left: 50%;
			transform: translateX(-50%);
			@include arr(9, 18, #808080, r ); } } }
.post {
	position: relative;
	&__title {
		font-size: 38px;
		font-weight: normal;
		font-family: $PEB;
		color: $white;
		position: absolute;
		left: 0;
		top: -180px;
		z-index: 3;
		padding: 0 15px;
		@include m {
			font-size: 30px;
			color: $black;
			position: relative;
			top: 0;
			margin-bottom: 20px;
			padding: 0; } }
	&__author, &__img-author {
		position: absolute;
		left: 0;
		top: -80px;
		width: 100%;
		z-index: 3;
		padding: 0 15px; }
	&__author {
		text-transform: uppercase;
		@include m {
			display: none; }
		a {
			color: $white;
			font-size: 13px; } }
	&__img-author {
		color: $white;
		text-align: right;
		font-size: 10px;
		text-transform: uppercase;
		top: -60px;
		@include m {
			display: none; } }
	&__socials {
		font-size: 0;
		margin-bottom: 10px;
		max-width: 660px;
		margin-left: auto;
		margin-right: auto;
		@include t {
			display: none; } }
	&__social {
		display: inline-block;
		color: $white;
		background: $gray;
		width: 95px;
		padding: 0 10px 0 15px;
		height: 42px;
		margin: 0 10px 0px 0;
		@include clr;
		amp-img {
			display: inline-block;
			margin-top: 12px;
			float: left; }
		span {
			font-size: 12px;
			float: right;
			margin-top: 14px; } }
	&__socials-footer {
		@include t {
			display: block; } }
	&__social-footer {
		@include m {
			width: 24%;
			margin-right: 1.3%;
			&:last-of-type {
				margin-right: 0; } } }
	&__text {
		blockquote {
			max-width: 690px;
			margin-left: auto;
			margin-right: auto;
			padding: 5px 5px 5px 10px;
			border-left: 2px #888 solid;
			background-color: #EEE; }
		p {
			padding: 10px 5px 10px 0;
			&:first-of-type {
				text-align: center; } }

		strong {
			font-family: $PB; }
		a {
			color: $red;
			border-bottom: $red 1px solid;
			transition: all .2s;
			&:hover {
				border-bottom-color: transparent; } }
		&>amp-img {
			margin: 25px auto;
			display: block; } }
	&__tags {
		margin-top: 20px;
		margin-bottom: 20px;
		font-size: 15px;
		text-transform: uppercase;
		color: $gray;
		a {
			color: $red; } }
	&__note {
		padding: 18px 0 28px;
		line-height: 1.2;
		font-size: 13px;
		color: $gray; } }
.block-title {
	font-size: 18px;
	color: $red-light;
	text-transform: uppercase;
	display: block; }
.similar {
	margin-top: 60px;
	border-top: #900022 1px solid;
	border-bottom: #900022 1px solid;
	padding-bottom: 10px;
	&__title {
		padding-top: 9px;
		margin-bottom: 7px; } }
.similar-article {
	padding: 8px 0 12px;
	&__title {
		font-size: 18px;
		font-family: $PB;
		transition: all .2s;
		&:hover {
			color: $red-light; } }
	&__date {
		font-size: 10px;
		color: $gray;
		display: inline-block;
		margin-top: 4px; } }
.comments-block {
	padding-top: 9px;
	margin-bottom: 100px;
	&__title {
		margin-bottom: 7px; }
	&__auth {
		font-size: 11px;
		text-transform: uppercase;
		color: $gray; }
	&__auth-btn {
		display: inline-block;
		padding: 0 15px;
		color: $red-light;
		line-height: 40px;
		border: $red-light 1px solid;
		margin-right: 7px;
		text-transform: uppercase; } }
.other-articles {
	font-size: 0;
	background: $gray-light;
	padding: 44px 0 100px;
	&__item {
		&:nth-of-type(odd) {
			margin-right: 4%; } }
	&__title {
		margin-bottom: 10px; } }
.other-article {
	display: inline-block;
	vertical-align: top;
	width: 48%;
	margin-bottom: 15px;
	&__title {
		font-size: 18px;
		transition: all .2s;
		font-family: $PB;
		margin-bottom: 10px;
		display: block;
		@include s {
			font-size: 14px; } }
	&__info {
		text-transform: uppercase;
		font-size: 10px;
		color: $gray;
		@include sm {
			display: none; } }
	&:hover {
		.other-article__title {
			color: $red; } } }
